    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@font-face {
    font-family: Cuyabra;
    src: local(Cuyabra), url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

@font-face {
    font-family: Monaspace_Radon;
    src: local(Monaspace_Radon), url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('otf');
