    height: 30px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    cursor: pointer;
}

.chat__videocamera:hover {
    background-size: 30px;
}

.chat__videocamera:active {
    background-size: 25px;
}

.message {
    display: flex;
    flex-direction: column;
    min-width: 220px;
    max-width: 95%;
    word-wrap: break-word;
    min-height: 30px;
    padding: 5px 10px;
    margin-bottom: 5px;
    border-radius: 15px;
    background-color: rgba(167, 63, 63, 0.2);
}

.favorite__container {
    border: 1px solid rgba(167, 63, 63, 0.2);
}

.message__header {
    color: grey;
    display: flex;
    justify-content: space-between;
}

.message__pin {
    width: 20px;
    height: 20px;
    border: none;
    background-color: transparent;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.message__pin:hover {
    background-size: 23px;
}

.message__pin:active {
    background-size: 18px;
}

.message__favorite {
    width: 20px;
    height: 20px;
    border: none;
    background-color: transparent;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

.message__favorite:hover {
    background-size: 22px;
}

.message__favorite:active {
    background-size: 18px;
}

.message__data {
    width: 88%;
    font-size: 12px;
    margin-left: 5px;
}

.message__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.image__container {
    margin: 3px;
    display: flex;
    flex-direction: column;
}

.audio__container {
    margin: 3px;
    display: flex;
    flex-direction: column;
}

.video__container {
    margin: 3px;
    display: flex;
    flex-direction: column;
}


.message__images-video {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}

.message__audio__container {
    display: flex;
    flex-direction: column;
}

.image__message {
    max-width: 300px;
    object-fit: contain;
}

.video__message {
    height: 500px;
}

.message__text {
    width: 90%;
    white-space: break-spaces;
}

.pinning__container {
    width: 100%;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pinning__msg {
    position: relative;
    background-color: rgba(167, 63, 63, 0.5), ;
    min-width: 80%;
}

.btn__pinning__message {
    position: absolute;
    top: 3px;
    right: 8px;
}

.pin__filesNames {
    display: flex;
    flex-direction: column;
    font-size: 13px;
}

.btn__pinning__message {
    border: none;
    background-color: transparent;
    color: red;
    font-size: 20px;
}

.btn__pinning__message:hover {
    font-size: 22px;
}

.btn__pinning__message:active {
    font-size: 18px;
}

.temp__files-container {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.temp__image-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.temp__image-container span {
    font-size: 14px;
    display: block;
    width: 90%;
}

.temp__image-item {
    margin: 5px;
    position: relative;
    background-color: #faf9f8;
    width: 15%;
}

.temp__img-image-item {
    width: 90%;
}

.temp__audio-item {
    margin: 5px;
    background-color: #faf9f8;
    width: 30%;
    position: relative;
}

.temp__audio-item span {
    font-size: 14px;
    display: block;
    width: 90%;
}

.temp__video-item {
    margin: 5px;
    background-color: #faf9f8;
    width: 20%;
    position: relative;
}

.temp__video-item span {
    display: block;
    width: 90%;
    font-size: 14px;
}

.temp__item-delete {
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    background-color: transparent;
    color: red;
    cursor: pointer;
    font-size: 18px;
}

.temp__item-delete:hover {
    color: #6e0c0c;
}

.temp__item-delete:active {
    font-size: 16px;
}

.temp__audio-container {
    background-color: #fff;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.temp__video-container video {
    width: 200px;
}

.drop__field {
    display: flex;
    justify-content: center;
    align-items: center;
    background: repeating-linear-gradient(45deg,
            rgba(167, 63, 63, 0.2),
            rgba(167, 63, 63, 0.2) 10px,
            rgba(167, 63, 63, 0.6) 10px,
            rgba(167, 63, 63, 0.6)20px);
    margin-left: 5%;
    width: 90%;
    height: 150px;
    border: 1px solid rgb(197, 123, 11);
}

.drop__field {
