, 0, 0.24);\n}\n\n.header__title {\n    cursor: default;\n}\n\n.header__title:hover,\n.favorites:hover {\n    text-shadow: 0.05em 0 0 #ec2225, -0.025em -0.05em 0 #313f97,\n        0.025em 0.05em 0 #50c878;\n    color: rgba(0, 194, 203, 0.2);\n}\n\n.search__input {\n    padding: 10px;\n    margin-left: 30px;\n    border: none;\n    font-size: 20px;\n    position: relative;\n    height: 90%;\n    max-height: 50px;\n}\n\n.search__input:focus {\n    border-radius: 15px;\n}\n\n.favorites {\n    background-color: transparent;\n    border: none;\n    font-size: 32px;\n    font-weight: 500;\n    cursor: pointer;\n\n}\n\n.favorites:active {\n    font-size: 28px;\n}\n\n.favorites__active {\n    border-bottom: 3px solid rgb(133, 28, 28);\n}\n\n.file__chaos {\n    cursor: pointer;\n}\n\n.chaos__container {\n    width: 100%;\n    position: relative;\n    border-left: 1px solid rgba(167, 63, 63, 0.2);\n    border-right: 1px solid rgba(167, 63, 63, 0.2);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    height: 90vh;\n}\n\n.chat {\n    /* display: flex; */\n    flex-direction: column;\n    align-items: baseline;\n    padding: 15px;\n    width: 100%;\n    height: 80%;\n    overflow-y: auto;\n}\n\n.chat__modal {\n    display: block;\n    text-align: center;\n    font-size: 14px;\n}\n\n.chat::-webkit-scrollbar {\n    width: 10px;\n}\n\n.chat::-webkit-scrollbar-track {\n    -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;\n    background-color: #f9f9fd;\n}\n\n.chat::-webkit-scrollbar-thumb {\n    background-color: rgba(167, 63, 63, 0.2);\n    background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, .25) 25%,\n            transparent 25%,\n            transparent 50%,\n            rgba(255, 255, 255, .25) 50%,\n            rgba(255, 255, 255, .25) 75%,\n            transparent 75%,\n            transparent);\n}\n\n.send__container::-webkit-scrollbar {\n    width: 10px;\n}\n\n.send__container::-webkit-scrollbar-track {\n    -web